.Menu {
    width: 100%;
    height: calc(100vh - 50px);

    overflow: scroll;
}

.Menu__header {
    width: 100%;
    margin: 20px 0;

    text-align: center;
    text-transform: uppercase;
    font-size: 1.6rem;
    font-weight: normal;
}

.Menu__list  {
    display: flex;
    flex-wrap: wrap;

    padding: 0;
    margin: 0;

    list-style: none;
}

.Menu__item {
    position: relative;

    width: 50%;

    background-repeat: no-repeat;
    background-size: cover;

    cursor: pointer;
}

.Menu__item:after {
    content: '';
    display: block;
    
    padding-bottom: 100%;
}

.Menu__item__content {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    width: 100%;
}

.Menu__item__title {
    margin: 0;

    font-size: 2rem;
    font-family: var(--font-family-title);
    letter-spacing: 1px;
}

.Menu__item__date {
    margin: 5px 0 0;
    font-size: 1.4rem;
}

@media screen and (min-width: 640px) {
    .Menu__item {
        width: 250px;
    }
}
